* {
  box-sizing: border-box;
}

h1, h2, h3, a, li, ul, p, body {
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background-color: #000;
}

header {
  text-align: center;
  padding-top: 50px;
  font-family: Great Vibes, cursive;
  font-style: normal;
  font-weight: 400;
}

.container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  margin: 50px 0;
  display: flex;
}

.card {
  width: 300px;
  height: 400px;
  animation: 3s infinite alternate shiver;
  position: relative;
}

.card__sides {
  border-radius: 25px;
  width: 100%;
  height: 100%;
  transition: transform 5s;
  position: absolute;
  transform: perspective(1000px);
  box-shadow: 0 0 15px #fff;
}

.card__sides--face {
  object-position: 50%;
  object-fit: cover;
}

.card__sides--back {
  color: #fff;
  backface-visibility: hidden;
  background-color: #00000080;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
  transform: perspective(1000px)rotateY(-180deg);
}

.card__title {
  text-align: center;
}

.card__subtitle {
  display: block;
}

.card:hover {
  animation: none;
}

.card:hover .card__sides--face {
  transform: perspective(1000px)rotateY(180deg);
}

.card:hover .card__sides--back {
  transform: perspective(1000px)rotateY(0);
}

@keyframes shiver {
  0% {
    transform: rotate(0);
  }

  10% {
    transform: rotate(-1deg);
  }

  20% {
    transform: rotate(1deg);
  }

  30% {
    transform: rotate(-2deg);
  }

  40% {
    transform: rotate(2deg);
  }

  50% {
    transform: rotate(-3deg);
  }

  60% {
    transform: rotate(3deg);
  }

  70% {
    transform: rotate(-2deg);
  }

  80% {
    transform: rotate(2deg);
  }

  90% {
    transform: rotate(-1deg);
  }

  100% {
    transform: rotate(0);
  }
}
